<template>
  <div id="purchase">
    <h3 style="text-align: center; color: red"></h3>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      :modal-append-to-body="false"
    >
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.date" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="form.address" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="add(form.address, form.date, form.name)"
          > </el-button
        >
      </div>
    </el-dialog>
    <div class="formtop">
      <div style="float: left; margin: 10px 30px 0px 50px">
        <span></span>
        <el-input
          v-model="inputval"
          :disabled="true"
          style="width: 200px"
        ></el-input>
      </div>
      <div style="float: left; margin: 10px 30px 10px 0px">
        <span></span>
        <el-date-picker v-model="value1" type="date" placeholder="">
        </el-date-picker>
      </div>
      <div style="float: left; margin: 10px 30px 10px 0px">
        <span></span>
        <el-select v-model="value" placeholder="">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </div>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData"
      border
      tooltip-effect="dark"
      style="width: 100%"
      height="450"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column prop="name" label="" width="120">
      </el-table-column>
      <el-table-column label="" width="120">
        <template slot-scope="scope">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column prop="name" label="" width="120">
      </el-table-column>
      <el-table-column prop="address" label="" width="120">
      </el-table-column>
      <el-table-column prop="name" label="" width="120"> </el-table-column>
      <el-table-column prop="name" label="" width="120"> </el-table-column>
      <el-table-column prop="name" label="" width="120"> </el-table-column>
      <el-table-column prop="name" label="" width="120"> </el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
            ></el-button
          >
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div style="float: right">
      <el-button
        style="margin: 10px 50px 10px 0px"
        type="primary"
        @click="dialogFormVisible = true"
        ></el-button
      >
      <el-button
        style="margin: 10px 50px 10px 0px"
        type="primary"
        @click="submit(inputval, value1, value)"
        ></el-button
      >
      <el-button
        style="margin: 10px 100px 10px 0px; width: 100px"
        type="primary"
        @click="recovery()"
        ></el-button
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "purchase",
  data() {
    return {
      tableData: [
        {
          date: "2016-05-03",
          name: "",
          address: " 1518 ",
        },
        {
          date: "2016-05-02",
          name: "",
          address: " 1518 ",
        },
        {
          date: "2016-05-04",
          name: "",
          address: " 1518 ",
        },
      ],
      multipleSelection: [],
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        name: "",
        address: "",
        date: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
      },
      formLabelWidth: "120px",
      options: [
        {
          value: "1",
          label: "",
        },
        {
          value: "2",
          label: "",
        },
        {
          value: "3",
          label: "",
        },
        {
          value: "4",
          label: "",
        },
        {
          value: "5",
          label: "",
        },
      ],
      value: "",
      value1: "",
      inputval: "test001",
    };
  },
  methods: {
    handleEdit(index, row) {
      alert(index, row);
    },
    handleDelete(index, row) {
      alert(index, row);
    },
    add(address, date, name) {
      var data = {
        date: date,
        name: name,
        address: address,
      };
      this.$data.tableData.push(data);
      this.$data.dialogFormVisible = false;
    },
    submit(inputval, value1, value) {
      alert(inputval+ value1+value);
    },
    recovery() {
      this.$data.tableData = [];
    },
  },
};
</script>

<style>
.formtop {
  width: 100%;
  min-width: 1200px;
}
</style>